
.NavigationBar{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.left-nav{
  display: flex;
  align-items: center;
}

.left-nav>p{
  color: #FFF;
  font-family: Varela;
  font-size: 2.1875rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
#Books{
  margin-top: 1.7rem;
  align-self: center;
  color: #FFF;
  font-family: Roboto;
  font-size: 1.25rem;
  font-style: normal;
  font-weight: 100;
  line-height: normal;
}

.nav-mid{
  display: flex;
  align-items: center;
  gap: 2rem;
}


.nav-mid>input{
  padding-left: 12px;
  color: #FFF;
  width: 42.375rem;
  height: 2.0625rem;
  flex-shrink: 0;
  border-radius: 0.25rem;
  background: #373737;
}

.nav-mid>button{
  width: 9.25rem;
  height: 2.0625rem;
  flex-shrink: 0;
  border: 1px solid rgba(255, 255, 255, 0.50);
  background: #292828;
  color: #FFF;
}
.right-nav{
  display: flex;
  align-items: center;
  gap: 1rem;
}
.right-nav>img{
  width: 3.125rem;
  height: 3.125rem;
  border-radius: 15.625rem;
  border: 2px solid #C3BEBE;
}

/* activebooks */

.active-book{
  display: flex;
  justify-content: center;
  align-items: center;
}
.books-showcase{
  margin-top: 8vh;
  padding-left: 5rem;
  width: 120rem;
  /* height: 75rem; */
  display: flex;
  gap: 3rem;
 
  background: #242121;
}

.showcase-book{
  display: flex;
  width: 28rem;
  height: 12rem;
  flex-shrink: 0;
  border-radius: 0.3125rem;
  background: rgba(113, 197, 244, 0.38);
  box-shadow: 0px 6px 4px 0px rgba(0, 0, 0, 0.25);
}
.showcase-book-image{
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}
.showcase-book-image img{
  position: relative;
  top: -25px;
  width: 10rem;
  height: 15rem;
  transform: rotate(-5deg);
  border-right: 12px solid #fff;
  margin-right: 8px; 
  border-radius: 5px;
  margin-bottom: 10px;
}

.showcase-book-info{
  display: flex;
  flex-direction: column;
  position: relative;
  
}

.showcase-book-info p{
  overflow: hidden;
  color: #D2D2D2;
  font-family: Roboto;
  font-size: 0.75rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  height: 2.5rem;
  
}

h4{
  margin: 0;
  margin-top: 6px;
  color: #FFF;
  font-family: Roboto;
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  overflow: hidden;
  height: 3.5rem;
}

.showcase-book-info a{
  position: absolute;
  top: 145px;
  left: 46px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  border: 1px solid #FFF;
  width: 9.25rem;
  height: 2.0625rem;
  flex-shrink: 0;
  color: #FFF;
  font-family: Roboto;
  font-size: 1.0625rem;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}


h3{
  color: #FFF;
  font-family: Roboto;
  font-size: 2.1875rem;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}


/* allbooks */

.allbooks{
  display: flex;
  padding: 24px;
  justify-content: center;
}
.allbooks img{
  width: 12rem;
  height: 18rem;
  flex-shrink: 0;
  border: 1px black solid;
}

.show-additonal-info{
  width: 80rem;
  height: 17rem;
  flex-shrink: 0;
  border-radius: 0.3125rem;
  background: rgba(113, 197, 244, 0.38);
  box-shadow: 0px 6px 4px 0px rgba(0, 0, 0, 0.25);
}





/* after click book  */

