  height: 17rem;
  transform: rotate(-5deg);
  margin-right: 8px; 
  border-radius: 5px;
  margin-bottom: 10px;
}
.book-title h3{
  color: #FFF;
  font-family: Roboto;
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  margin: 0.5rem;
  height: 2.2rem;
  overflow: hidden;
}
.book-title p{
  color: rgba(255, 255, 255, 0.50);
  font-family: Roboto;
  font-size: 1.375rem;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  margin: 0.5rem;
}
.book-body>p{
  color: rgba(255, 255, 255, 0.50);
  font-family: Roboto;
  font-size: 1.25rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  overflow: hidden;
  height: 3rem;
  margin: 0.5rem;
}

.book-ratings{
  display: flex;
  gap: 4rem;
  color: #E2E2E2;
  font-family: Roboto;
  font-size: 1.25rem;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  margin: 0;
}

.book-info-description{
  height: 3rem;
  overflow: hidden;
}

.addition-info{
  display: flex;
  gap: 24px;
}
.now-read-pos{
  position: absolute;
  left: 50rem;
}

.addition-info p{
  color: #E2E2E2;
  font-family: Roboto;
  font-size: 1.25rem;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.book-date{
  position: relative;
  top: -3rem;
  left: 55rem;
  color: rgba(255, 255, 255, 0.50);
  font-size: 1.375rem;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}

.book-buttons{
  display: flex;
  gap: 5rem;
  position: relative;
}

.now-read{
  position: absolute;
  left: 50rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  border: 1px solid #FFF;
  width: 9.25rem;
  height: 2.0625rem;
  flex-shrink: 0;
  color: #FFF;
  font-family: Roboto;
  font-size: 1.0625rem;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}

.moreinfo{
  position: absolute;
  left: 62rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  border: 1px solid #FFF;
  width: 9.25rem;
  height: 2.0625rem;
  flex-shrink: 0;
  color: #FFF;
  font-family: Roboto;
  font-size: 1.0625rem;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}




































/* loader  */
.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  top: 180px;
  left: 45rem;
  font-size: 45px;
  text-indent: -9999em;
  overflow: hidden;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  position: relative;
  transform: translateZ(0);
  animation: mltShdSpin 1.7s infinite ease, round 1.7s infinite ease;
}

@keyframes mltShdSpin {
  0% {
    box-shadow: 0 -0.83em 0 -0.4em,
    0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em,
    0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
  5%,
  95% {
    box-shadow: 0 -0.83em 0 -0.4em, 
    0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 
    0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
  10%,
  59% {
    box-shadow: 0 -0.83em 0 -0.4em, 
    -0.087em -0.825em 0 -0.42em, -0.173em -0.812em 0 -0.44em, 
    -0.256em -0.789em 0 -0.46em, -0.297em -0.775em 0 -0.477em;
  }
  20% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em,
     -0.555em -0.617em 0 -0.44em, -0.671em -0.488em 0 -0.46em, 
     -0.749em -0.34em 0 -0.477em;
  }
  38% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em,
     -0.645em -0.522em 0 -0.44em, -0.775em -0.297em 0 -0.46em, 
     -0.82em -0.09em 0 -0.477em;
  }
  100% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 
    0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
}

@keyframes round {
  0% { transform: rotate(0deg) }
  100% { transform: rotate(360deg) }
}
 